import { Injectable } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { Router } from '@angular/router';
import { ToastController } from '@ionic/angular';
import firebase from 'firebase/app';
import { GooglePlus } from '@ionic-native/google-plus/ngx';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  loggedIn: boolean;
  authState: any = null;

  constructor(private fireAuth: AngularFireAuth, private toastController: ToastController,
    private router: Router, private googlePlus: GooglePlus) {
    }

    isLoggedIn(){
      //check session storage if user is still logged in
      if(sessionStorage.getItem('user') != null){
        //user is logged in
        this.loggedIn = true;
      }
      else{
        //user is not logged in
        this.loggedIn = false;
      }
      return this.loggedIn;
    }

    register(email, password) {
      return this.fireAuth.createUserWithEmailAndPassword(email, password)
       .then(() => {
      const toast = this.toastController.create({
      message: 'Registeration successful!',
      duration: 3000,
      position: 'bottom'
      }).then(alert => alert.present());
      this.router.navigate(['/login']);
      }).catch((error) => {
        const toast = this.toastController.create({
          message: error,
          duration: 3000,
          position: 'bottom'
          }).then(alert => alert.present());
      })
    }

    login(email, password) {
      return this.fireAuth.signInWithEmailAndPassword(email, password)
      .then(async (user) => {
        if (user) {
          const toast = this.toastController.create({
          message: 'Login successful!',
          duration: 2000,
          position: 'bottom'
          }).then(alert => alert.present());
          sessionStorage.setItem('user', (await this.fireAuth.currentUser).uid);
          this.router.navigate(['/tabs/tab4']).then(()=>
          {
            window.location.reload();
          })
        }
      }).catch((error) => {
        const toast = this.toastController.create({
          message: error,
          duration: 3000,
          position: 'bottom'
          }).then(alert => alert.present());
      })
    }

    //no idToken field in response???
    async googleLogin(){
      await this.googlePlus.login({})
      .then(res => {
        console.log(JSON.stringify(res));
        const googleCredential = firebase.auth.GoogleAuthProvider.credential(res.idToken, res.accessToken);

        this.fireAuth.signInWithCredential(googleCredential).then(res =>{
          console.log(res);
          console.log("login successful");
          this.router.navigate(['/tabs']);
        }).catch(err=>{
          console.log(JSON.stringify(err));
        });
    })
  }

    getUserId(){
      return sessionStorage.getItem("user");
    }
    
    logout() {
      console.log("Logout successful!")
      return this.fireAuth.signOut().then(() => {
        sessionStorage.removeItem('user');
        location.reload();
         const toast = this.toastController.create({
          message: 'Logout successful!',
          duration: 2000,
          position: 'bottom'
          }).then(alert => alert.present());
      });
    }
}